@import "../assets/variables/variables";

.skillsBlock {
  min-height: 100vh;
  padding: 50px 0;
  display: flex;
  align-items: center;
  background: $bgSkills;
}


.skillsContainer {
  .title {
    margin: 50px auto;
  }
}



.skills {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 100%;
}

@media (max-width: 576px) {
  .skillsBlock {
    padding-top: 50px;
  }
}