.nav {
  width: 350px;
  display: flex;
  justify-content: space-between;
  a {
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Oswald-Regular', sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    transition: all .4s ease-out;

    cursor: pointer;

    &:hover {
      color: rgb(37, 172, 202)
    }
  }
}
.nav > .active {
  color: rgb(37, 172, 202)
}

@media (max-width: 830px) {
  .nav {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media (max-width: 576px) {
  .nav {
    padding: 0;
  }
}