.project {
  width: 33.33%;
  flex-basis: 330px;
  display: flex;
  flex-direction: column;
  border-radius: 10px 10px 0 0;
  position: relative;
  margin-top: 40px;
  & > .hoverEffect {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0;
    border-radius: 10px;
    -webkit-transition: opacity 0.35s ease-out;
    transition: opacity 0.35s ease-out;
  }
  &:hover .hoverEffect {
    opacity: 0.75;
  }
}

.projectImg {
  max-width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px 10px 0 0;
  background-color: #030303;
  .watchBtn {
    position: relative;
    z-index: 5;
    cursor: pointer;
    margin: 100px 0;
    opacity: 0;
    transition: opacity 0.35s ease-out;
  }
}



.project:hover .projectImg .watchBtn {
  opacity: 1;
}

.projectText {
  position: relative;
  z-index: 5;
  display: block;
  padding: 15px 0 15px 0;
  background-color: #030303;
  border-radius: 0 0 10px 10px;

  h2 {
    margin: 10px 0;
    font-size: 23px;
    text-transform: uppercase;
    font-family: 'Oswald-Medium', sans-serif;
  }
}

@media screen and (max-width: 1350px) {
  .project {
    flex-basis: 300px;
    .watchBtn {
      margin: 75px 0;
    }
  }

}

@media (max-width: 768px) {
  .project {
    flex-basis: 100%;
    margin-bottom: 30px;
    .watchBtn {
      padding: 40px 0;
    }
  }


  .project:last-child {
    margin-bottom: 0;
  }
}
